Exotic.uploadFile = function(file, callback){
	let that = this;
    let formData = new FormData();
    formData.append('file',file);
    formData.append('fileType','image');
    formData.append('fileModule','product');
    formData.append('isZoom','0');
	$.ajax({
        type: 'post',
        url: that.url + that.urlMap.other.uploadFile,
        data: formData,
        processData: false,
        contentType: false,
        success:function(data){
            if (data.code == 0) {
                that.tip({txt:data.message});
                if(callback){
                    callback(data);
                }
            } else {
                Indicator.close()
                that.tip({txt:data.message});
            }
        }
    })
}
