<template>
  <div class="combobox">
      <div class="select" :id="sid" @click.stop="type==opentype.detail?rt():sh()">
          <div class="select-title">
              {{init.text}}
          </div>
      </div>
      <i></i>
      <mt-popup
        style="width:100%;"
        v-model="init.key"
        position="bottom"
        :closeOnClickModal="true"
        popup-transition="popup-fade">
          <slot name="box"></slot>
      </mt-popup>
  </div>
</template>
<style scoped lang="scss">
.combobox {
    width: 100%;
    height: 24px;
    position: relative;
  }
  .combobox .select {
    width: 100%;
    height: 24px;
    line-height: 24px;
    outline: none;
    font-size: 14px;
  }
  .combobox i {
    width: 12px;
    height: 12px;
    display: block;
    background: url(../../assets/icon-xb.png) right bottom no-repeat;
    position: absolute;
    background-size: 6px;
    bottom: 0;
    right: 0;
    z-index: 2;
  }
  .pup {
    padding: 14px 0;
    font-size: 16px;
  }
</style>

<script>
import Exotic from '../../config.js';
    export default{
        name:'Combobox',
        data(){
            return{
                sid:'',
                opentype:{add:"1",update:"2",detail:"3"}
            }
        },
        methods:{
            sh(){
                if(this.init.key==true){
                    document.getElementsByTagName('html')[0].style.overflow="hidden"
                    document.getElementsByTagName('body')[0].style.overflow="hidden"
                }else{
                    document.getElementsByTagName('html')[0].style.overflow="auto"
                    document.getElementsByTagName('body')[0].style.overflow="auto"
                }
                this.init.key=!this.init['key'];
            },
            rt(){
                return true;
            }
        },
        props:['init','type'],
        created(){
            this.sid=this.init.id||'';
        },
        mounted(){
            window.onclick=function(){
               this.init.key=false;
            }.bind(this);
        }
    }
</script>



