// 把路由对应的组件定义成异步组件
const AddGoodView = resolve => {
    require.ensure(['./components/AddGood.vue'], () => {
        resolve(require('./components/AddGood.vue'))
    })
}
const AddActivityView = resolve => {
    require.ensure(['./components/AddActivity.vue'], () => {
        resolve(require('./components/AddActivity.vue'))
    })
}
const AddCardView = resolve => {
    require.ensure(['./components/AddCard.vue'], () => {
        resolve(require('./components/AddCard.vue'))
    })
}
const AddBulkView = resolve => {
    require.ensure(['./components/AddBulk.vue'], () => {
        resolve(require('./components/AddBulk.vue'))
    })
}
const AddSpecificationsView = resolve => {
    require.ensure(['./components/AddSpecifications.vue'], () => {
        resolve(require('./components/AddSpecifications.vue'))
    })
}
const AddTopicView = resolve => {
    require.ensure(['./components/AddTopic.vue'], () => {
        resolve(require('./components/AddTopic.vue'))
    })
}
const GroupbuyOrderView = resolve => {
    require.ensure(['./components/GroupbuyOrder.vue'], () => {
        resolve(require('./components/GroupbuyOrder.vue'))
    })
}
const OrderDetailsView = resolve => {
    require.ensure(['./components/OrderDetails.vue'], () => {
        resolve(require('./components/OrderDetails.vue'))
    })
}
const CommentView = resolve => {
    require.ensure(['./components/Comment.vue'], () => {
        resolve(require('./components/Comment.vue'))
    })
}
const routes=[
    {
        path:'/',
        redirect:'/addgood'
    },{
        path:'/addcard',
        component:AddCardView
    },{
        path:'/addgood',
        component:AddGoodView
    },{
        path:'/addactivity',
        component:AddActivityView
    },{
        path:'/addbulk',
        component:AddBulkView
    },{
        path:'/addspecifications',
        component:AddSpecificationsView
    },{
        path:'/addtopic',
        component:AddTopicView
    },{
        path:'/groupbuyorder',
        component:GroupbuyOrderView
    },
    {
        path:'/orderdetail',
        component:OrderDetailsView
    },{
        path:'/comment',
        component:CommentView
    }
]

export default routes;


